}

/* Code for message dropdown */
.partner-dropdown:hover {
  cursor: pointer;
}

.fa-stack[data-count]:after {
  position: absolute;
  right: 4%;
  top: -10px;
  content: attr(data-count);
  font-size: 76%;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  color: white;
  background: #ff9900;
  text-align: center;
  min-width: 2em;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.partner-dropdown {
  position: relative;
  display: inline-block;
}

.partner-dropdown-content {
  display: none;
  position: absolute;
  right: 2px;
  background-color: #ffffff;
  min-width: 140px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
}

.partner-dropdown:hover .partner-dropdown-content {
  display: block;
}

/* End of code for message dropdown */

