.editable-image-container {
  position: relative;
}

.editable-image {
  opacity: 1;
  transition: 0.5s ease;
  backface-visibility: hidden;
}

.editable-middle {
  transition: 0.5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
}

.editable-image-container:hover .editable-image {
  opacity: 0.4;
}

.editable-image-container:hover .editable-middle {
  opacity: 1;
  cursor: pointer;
}

.change-image-caption {
  background-color: #262626;
  font-size: 16px;
  padding: 10px;
  border: 1px solid #ccc;
  width: 48px;
  height: 48px;
  display: inline-block;
  overflow: hidden;
  cursor: pointer;
}

#searchrequest {
  padding: 2px 10px 2px 24px;
  margin-right: 1%;
  border-radius: 0.2rem;
  color: blue;
  background-image: url('https://www.w3schools.com/css/searchicon.png');
  background-repeat: no-repeat;
  background-position: left;
  outline: 0;
}

#mymodal {
  display: none;
}

.editable-meals {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  border: none;
  border-color: transparent;
  border-bottom: #717171 1px solid;
  cursor: pointer;
}

[contenteditable] {
  outline: 0px solid transparent;
}

.gradient-custom-2 {
  background: #fccb90;
  border-top-left-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;
  background: -webkit-linear-gradient(to bottom right, #ff9900, #ff6600);
  background: linear-gradient(to bottom right, #ff9900, #ff6600);
}

@media (min-width: 768px) {
  .gradient-form {
    height: 100vh !important;
  }
}

.crop {
  white-space: nowrap;
  width: 20em;
  overflow: hidden;
  text-overflow: ellipsis;
}

.popup {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.15);
  overflow-y: hidden;
}

.scrollable-popup {
  position: 'absolute';
  top: 10%;
  left: 35%;
  width: 400;
  height: calc(100vh-100px);
  overflow-y: scroll;
}

.img-frame {
  display: flex;
  justify-content: center;
  align-items: center;
  border: #a7b1c2 1px solid;
  border-radius: 4px;
