  height: 108;
}

.sticky-bottom {
  position: absolute;
  bottom: 28px;
  right: 20px;
}


/* Define font style for table headings */
.card-dashboard-table thead th {
  font-weight: bold;
  font-size: 12px;
  text-transform: capitalize;
}

/* Adjust column width for specific columns */
.card-dashboard-table th:nth-child(1) {
  /* First column width */
  width: 25%;
}

.card-dashboard-table th:nth-child(2),
.card-dashboard-table th:nth-child(3),
.card-dashboard-table th:nth-child(4),
.card-dashboard-table th:nth-child(5),
.card-dashboard-table th:nth-child(6) {
  width: 15%;
}

/* Style headings and values within ibox-tools */
.ibox-tools .custom-ibox-tools {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Style left-aligned headings */
.ibox-tools div span:first-child {
  text-align: left;
  font-weight: bold;
  margin-right: 10px;
  /* Add spacing between heading and value */
}

/* Style right-aligned values */
.ibox-tools div span:last-child {
  text-align: right;
  font-weight: normal;
  /* If needed to reset font weight */
}